<template>
  <div class="member-list">
	  <div class="top">任务分配给指定员工负责，对方在任务开始和快过期会收到 推送.</div>
    <div class="tit">负责人、管理员</div>
    <ul class="list-item">
      <li v-for="(item,index) in adminList" @click="chosenMember(item)">
        <span class="name-box" :class="'color'+genHash(item.realName)">{{nameFormate(item.realName)}}</span>
        <span class="name">{{nameFormate(item.realName)}}</span>
        <i v-show="item.isChosen" class="is-chosen"></i>
      </li>
    </ul>
    <div class="tit">成员</div>
    <ul class="list-item">
      <li v-for="(item,index) in memberList" @click="chosenMember(item)">
        <span class="name-box" :class="'color'+item.randomcolor">{{nameFormate(item.realName)}}</span>
        <span class="name">{{nameFormate(item.realName)}}</span>
        <i v-show="item.isChosen" class="is-chosen"></i>
      </li>
    </ul>
  </div>
</template>
<script>
import { listMethods } from "@/mixins/list";
export default {
  mixins: [listMethods],
  name: 'memberList',
  data () {
    return {
      page:0,
      size:10,
      adminList:[],
      memberList:[],
      chosenList:[{'id':'','name':''}]
    }
  },
  methods:{
    genHash (str){
      let hash = 5381;
      str = str || '';
      for(let i=0, len=str.length; i<len; ++i){
          hash += (hash << 5) + str.charAt(i).charCodeAt();
      }
      return (hash & 0x7fffffff)%12;
    },
    chosenMember(item){
      if(this.getUrlParam("isSelect")=='false'){
        return ;
      }else{
        item.isChosen=!item.isChosen
        if(item.isChosen){
          this.chosenList.push({
            'id':item.id,
            'realName':item.realName,
          })
        }else{
          this.chosenList.map((item2,index2)=>{
            if(item2.id==item.id){
              this.chosenList.splice(index2,1)
            }
          })
        }
        console.log(JSON.stringify(this.chosenList));
        window.webkit.messageHandlers.avatarClick.postMessage(JSON.stringify(this.chosenList))
      }
    },
    getMemberlist(that,page){
      var idsArr=[]
      this.chosenList.shift()
      if(that.getUrlParam("ids")){
        idsArr= that.getUrlParam("ids").split(",")
      }
      that.$ajax({
        method: 'GET',
        url: `${that.api}/api/user?page=${page}&size=${that.size}&sort=createTime,desc`
       }).then(function (response) {
  			  response.data.content.map((item,index)=>{
            item.randomcolor=parseInt(Math.random()*12+1,10);
            item.isChosen=false
            if(idsArr.includes((""+item.id))){
              item.isChosen=true
              that.chosenList.push({
                'id':item.id,
                'realName':item.realName,
              })
            }
            if(item.userType ==0){
              that.memberList.push(item)
            }
            if(item.userType ==1){
              that.adminList.push(item)
            }
          })
       }).catch(function (error) {
          console.log(error);
       });
    }
  },
  created(){
    this.getMemberlist(this,this.page)
  }
}
</script>



